#skills {
    overflow: hidden;
    width: 100vw;
    max-width: 85rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}
.skillTitle {
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}
.skillDesc {
    font-weight: 400;
    font-size: 1rem;
    max-width: 50rem;
    padding: 0 2rem;
}
.skillBars {
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}
.skillbar {
    align-items: center;
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius:0.5rem;
    background: rgb(50, 50, 50);
}
.skillbarIMG {
    width: 9rem;
    object-fit: cover;
    margin-right: 2rem;
}
.skillbarTEXT{
    font-size: 1.5rem;
}
.skillbarTEXT>h2{
    margin-bottom: 0.4rem;
    margin-top: 0.4rem;
}
.skillbarTEXT>p {
    font-size: 1.2rem;
    font-weight: 200;
}

@media screen and (max-width:720px) {
    .skillbarTEXT>p {
        font-size: 2vw;
    }
}
@media screen and (max-width:480px) {
    .skillbarTEXT>p {
        font-size: 3vw;
    }
    .skillbarIMG{
        width: 2.25rem;
        height: 2.25rem;
    }
}